
.form-card1 {
    margin-top: 60px;
}


.form {
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: center;
    font-family: inherit;
    gap: 10px;
    padding-inline: 2em;
    padding-bottom: 1em;
    background-color: #FF8C9E;
    background-color: #FF4E88;
    border-radius: 20px;
    height: 400px;
  }
  
  .form-heading {
    text-align: center;
    margin: 1em;
    color: #FFDA76;
    font-size: 18px;
    background-color: transparent;
    align-self: center;

  }
  .input-field::placeholder {
    color: rgb(0, 0, 0);
    
  }
  .form-field {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    border-radius: 10px;
    padding: 0.6em;
    border: none;
    outline: none;
    color: rgb(255, 255, 255);
    background-color: #FF8C9E;
    box-shadow: inset 2px 5px 10px rgb(5, 5, 5);
    height: 40px;
    text-align: center;
  }
  
  .input-field {
    background: none;
    border: none;
    outline: none;
    width: 100%;
    color: #000000;
    padding-inline: 1em;
  }
  
  .sendMessage-btn {
    cursor: pointer;
    margin-bottom: 10px;
    padding: 15px;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: transparent;
    color: white;
    font-weight: bold;
    outline: 1px solid #FFDA76;
    transition: all ease-in-out 0.3s;
    width: 200px;
    position: relative;
    left: 95px;
  }
  
  .sendMessage-btn:hover {
    transition: all ease-in-out 0.3s;
    background-color: #FFDA76;
    color: #000;
    cursor: pointer;
    box-shadow: inset 2px 5px 10px rgb(5, 5, 5);
  }
  
  .form-card1 {
    background-image: linear-gradient(163deg, #125B9A 0%, #FFDA76 100%);
    border-radius: 22px;
    transition: all 0.3s;
  }
  
  .form-card1:hover {
    box-shadow: 0px 0px 30px 1px rgba(100, 255, 218, 0.3);
  }
  
  .form-card2 {
    border-radius: 0;
    transition: all 0.2s;
  }
  
  .form-card2:hover {
    transform: scale(0.98);
    border-radius: 20px;
  }
  