* {
  text-decoration: none;
  font-size: 21px; 
  font-family: 'Times New Roman', Times, serif;
  
  }
  

body {
  background-image: url('./background.jpg'); 
  background-size: cover;
  background-repeat: no-repeat;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}
.about  h1, p {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about h1 {
  text-align: center;
  position: relative;
  font-size: 100px;
  color: white;
}

.about p {
  text-align: center;
  position: relative;
  top: 100px;
  font-size: 50px;
  color: white;
}



.contact {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact h1 {
  text-align: center;
  font-size: 200px;
  color: white;
}
.home  {
  display: flex;
  justify-content: center;
  align-items: center;
}

.home h1 {
  text-align: center;
  font-size: 200px;
  color: white;
}

.navdiv {
  background-color: #ffffff;
  color: rgb(17, 17, 17);
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  padding: 0 1rem;
}

.navbar ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
  

}

.navbar a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

ul {
  display: flex;
  gap: 30px;
  list-style-type: none;
}


.navbar li.active {
  background-color: #f37ad1;
}

.navbar li:hover {
  background-color: #777;
}











